@import url(http://fonts.googleapis.com/css?family=Roboto+Condensed:400,700);
/* ------------------------------- *
 *              Menu               *
 * ------------------------------- */
#menu {
  position: fixed;
  top: 0;
  left: 0;
  height: 40px;
  z-index: 70;
  width: 100%;
  padding: 0;
  margin: 0;
}

#menu li {
  display: inline-block;
  margin: 5px;
  color: #000;
  background: #fff;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 10px;
}

#menu li a {
  text-decoration: none;
  color: #000;
  padding: 8px 16px;
  display: block;
}

#menu li:hover {
  background: rgba(255, 255, 255, 0.8);
}

#menu li.active {
  background: #666;
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
}

#menu li.active a {
  color: #fff;
}

#menu li.active a:hover {
  color: #000;
}

#menu li.active:hover {
  background: #fff;
}

#fullpage p {
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 5vw;
  font-size: 7vw;
  -webkit-text-shadow: 0 0.3rem 0.9rem rgba(0, 0, 0, 0.4);
  text-shadow: 0 0.3rem 0.9rem rgba(0, 0, 0, 0.4);
  letter-spacing: 2px;
  color: #fff;
}

#fullpage p a {
  color: #fff;
  text-decoration: none;
}

#fullpage pre {
  font-size: 1.5em;
}

#fullpage .user-img {
  width: 60%;
  border-radius: 100%;
}

#fullpage #home {
  text-align: center;
}

#fullpage #home .fp-tableCell {
  background-image: url("../../public/images/img2.jpg");
  background-size: cover;
  background-position: center 80%;
}

#fullpage #profile .fp-tableCell {
  background-image: url("../../public/images/img1.jpg");
  background-size: cover;
  background-position: center 80%;
}

#fullpage #skills .fp-tableCell {
  background-image: url("../../public/images/img4.jpg");
  background-size: cover;
  background-position: center 80%;
}

#fullpage #projects .fp-tableCell {
  background-image: url("../../practice/images/banner2.jpeg");
  background-size: cover;
  background-position: center 80%;
}

/* ------------------------------- *
 *             loading             *
 * ------------------------------- */
#loading {
  background-color: #fff;
  display: block;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 9999;
}

#loading > #loader {
  position: absolute;
  top: 50%;
  margin-top: -75px;
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  font-family: 'Nunito', sans-serif;
  font-size: 4.6875em;
  color: transparent;
  letter-spacing: 0.01em;
}

#loading > #loader > span {
  text-shadow: 0 0 2px rgba(204, 208, 212, 0.9), 0 15px 25px rgba(0, 0, 0, 0.3), 0 -2px 3px rgba(0, 0, 0, 0.1), 0 -5px 10px rgba(255, 255, 255, 0.5), 0 5px 10px rgba(0, 0, 0, 0.3), 0 3px 4px rgba(255, 255, 255, 0.2), 0 0 20px rgba(255, 255, 255, 0.45);
  -webkit-animation: loading 0.85s ease-in-out infinite alternate;
  animation: loading 0.85s ease-in-out infinite alternate;
}

#loading > #loader > span:nth-child(2) {
  -webkit-animation-delay: .1s;
  animation-delay: .1s;
}

#loading > #loader > span:nth-child(3) {
  -webkit-animation-delay: .2s;
  animation-delay: .2s;
}

#loading > #loader > span:nth-child(4) {
  -webkit-animation-delay: .3s;
  animation-delay: .3s;
}

#loading > #loader > span:nth-child(5) {
  -webkit-animation-delay: .4s;
  animation-delay: .4s;
}

#loading > #loader > span:nth-child(6) {
  -webkit-animation-delay: .5s;
  animation-delay: .5s;
}

#loading > #loader > span:nth-child(7) {
  -webkit-animation-delay: .6s;
  animation-delay: .6s;
}

@-webkit-keyframes loading {
  to {
    text-shadow: 0 0 2px rgba(204, 208, 212, 0.2), 0 0 3px rgba(0, 0, 0, 0.02), 0 0 0 transparent, 0 0 0 rgba(255, 255, 255, 0), 0 0 0 transparent, 0 0 0 rgba(255, 255, 255, 0), 0 0 0 rgba(255, 255, 255, 0);
  }
}

@keyframes loading {
  to {
    text-shadow: 0 0 2px rgba(204, 208, 212, 0.2), 0 0 3px rgba(0, 0, 0, 0.02), 0 0 0 transparent, 0 0 0 rgba(255, 255, 255, 0), 0 0 0 transparent, 0 0 0 rgba(255, 255, 255, 0), 0 0 0 rgba(255, 255, 255, 0);
  }
}

